form{
    display: flex;
    flex-direction: column;
    border: 1px solid #3333333f;
    gap: 10px;
    width: 100%;
    padding: 10px;
    max-width: 500px;
    margin: 40px auto;
}

label{
    display: flex;
    gap: 10px;
}

input:not([type=checkbox]){
    padding: 8px;
    width: 95%;
}

input[type=submit]{
max-width: 120px;
background-color: #276AAF;
color: #FFF;
border: none;
font-family: 'Poppins';
border-radius: 5px;

}

.image_box img{
    max-width: 200px;
}