
.grid{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
.image_box{
    max-width: 200px;
    padding: 20px;
    aspect-ratio: 1/1;
    display: flex;
    flex-direction: column;
    gap: 20px;
    cursor: pointer;
}

.image_box p {
    font-size: 11px;
}

.image_box:hover{
    transform: scale(1.03);
    
}