.logo_container{
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #276AAF;
    font-size: 12px;
}

.curiosidades{
    border: 2px solid;
    display: flex;
    
    background-color: #276baf8e;
    color: #FFF;
    font-weight: bold;
}
.item{
    border: 1px solid #ffffff46 ;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    padding: 15px;
}
.item i {
    font-size: 30px;
    color: #276baf;
    text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.144);
}

.recicle_container{
    gap: 10px;
}

@media(max-width:768px){
    .curiosidades{
        flex-direction: column;
        
    }

    .item{
        border-bottom: 1p;
    }
}
