@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500&display=swap');

*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Poppins';
}
li{
  list-style: none;
}

.flex,.flex-column{
  display: flex;

}

.flex-column{
  flex-direction: column;
}

.small{
  font-size: 13px;
}
hr{
  margin: 40px 0 ;
}

.center{
  align-items: center;
  justify-content: center;
}
a{
  color: #000;
  text-decoration: none;
}
.btn-recicle{
  border: 1px solid #000;
  padding: 8px 25px;
  border-radius: 20px;
  box-shadow: 4px 4px 4px black;
  cursor: pointer;
}
.btn-recicle:hover{
  background-color: #c9c8c8;
}

.big{
  font-size: 22px;
}

.gp-10{
  gap: 10px;
}

.form_container{
  display: flex;
  flex-direction: column;
  align-items: center;
}

img{
  max-width: 100%;
  height: 100%;
}